    padding: 0;
    margin: 0;
    /* border: 1px red solid; */
    box-sizing: border-box;
}

.container {
    width: min(100vw,1440px);
    /* padding-top: 10px; */
    margin: 0 auto 30px;
}

ul,li,a{
    list-style: none;
    text-decoration: none;
}

header,header a {
    background-color: rgb(0, 132, 255);
    height: min(40vh,50px);
    color: #ffffff;
}

header>div {
    position: sticky;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: min(1.5rem,calc(2.5vw + 5px));
    padding: 10px;
}

.logo {
    flex-grow: 1;
    display: flex;
    gap: 10px;
    align-items: center;
}

.logo>div {
    border-top: greenyellow 4px solid;
    border-right: yellow 4px solid;
    border-bottom: blue 4px solid;
    border-left: red 4px solid;
    width: 20px;
    height: 20px;
    animation: spin 2000ms infinite linear alternate;
}

nav {
    flex-grow: 2;
}

nav>ul{
    display: flex;
    width: 100%;
    justify-content: space-between;
}


.skewed {
    padding: 15px;
    height: 55vh;
    width: 100%;
    background-color: rgb(52, 52, 248);
    position: relative;
    z-index: -1;
    transform: skewY(-8deg);
    transform-origin: calc(0% - 80px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.skewed>* {
    transform: skewY(8deg);
}

.skewed>div{
    margin-top: 260px;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.skewed>div>div:first-of-type{
    width: clamp(420px,);
    height: 100%;
    display: flex;
    flex-direction: column;
}

.skewed img{
    /* max-width: 420px; */
    height: 100%;
    border-radius: 10px;
    position: relative;
    box-shadow: 3px 3px 9px black;
}

.skewed>div>div:first-of-type>p{
    /* color: rgb(52, 52, 248); */
    text-shadow: 2px 2px 6px #ffffff;
    font-size: calc(1.5rem + 5px);
    z-index: 1;
    margin-top: -40px;
    text-align: center;
}


.about {
    /* margin-top: 160px; */
    background-color: white;
    width: 45%;
    padding: 15px 15px 15px 45px;
    border-radius: 0 10px 10px  0 ;
    box-shadow: 3px 3px 9px black;
    z-index: -1;
    transform: translateX(-30px);
}

.about>h2{
    font-size: calc(1.5vw + 5px);
    margin-bottom: 20px;
}

.about>p{
    font-size: min(1.5rem,calc(2.5vw + 2px));
}

.icons>ul{
    display: flex;
    gap: 20px;
    justify-content: end;
    margin: 35px 0 10px 0;
}

.icons a,i{
    color: rgb(52, 52, 248);
    font-size: 2vw;
}

.frontend {
    padding: 5px;
    background-color: aqua;
    display: inline-block;
    animation: slide 2000ms 1 alternate  ease-out;
}

.frontend:window-inactive {
    animation: slide 2000ms 1 alternate  ease-out;
}

section {
    margin-top: 60px;
    padding: 10px;
}

.works {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    gap: 10%;
}

.works>div{
    width: max(30%,min(500px,80vw));
    height: 690px;
    padding-bottom: 10px;
    margin-top: 20px;
    box-shadow: 2px 2px 9px;
    border-radius: 2%;
}

.title {
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.works>div>div{
    padding: 10px;
    font-size: min(20px,calc(.9vw + 5px));
    /* margin-bottom: 20px; */
    /* font-size: calc(1vw + 3px); */
}

.works>div>img {
    height: 75%;
    width: 100%;
}

footer {
    height: max(70vh,800px);
    color: #ffffff;
}

address{
    font-size: min(25px,calc(1.5vw + 5px));
}

footer>.container{
    padding: calc(3vw + 5px);
    height: 100%;
    background-color: rgb(0, 132, 255);
    margin-bottom: 0;
    display: flex;
    gap: 5%;
}

footer>.container>div {
    width: 45%;
    height: 80%;
}

footer>.container>div:first-of-type {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

footer>.container>div:first-of-type>h3 {
    font-size: min(45px,calc(2.5vw + 5px));
    margin-bottom: 15px;
}

footer>.container>div:first-of-type>div {
    flex-grow: 1;
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

footer>.container>div:first-of-type>div>p:first-of-type {
    font-size: min(25px,calc(1.5vw + 5px));
}

footer>.container>div:first-of-type>div a {
    font-size: min(25px,calc(1.5vw + 5px));
}

footer i{
    margin: 10px;
    color: #ffffff;
}

footer ul {
    display: flex;
    gap: 20px;
}

footer>.container>div:last-of-type {
    display: flex;
    align-items: center;
    justify-content: end;
}

footer>.container>div:last-of-type>img {
    width: 100%;
}

@keyframes spin {
    0% {
        transform: rotate3d(0,0,0,180);
    }
    33% {
        transform: rotate3d(1,0,0,180deg);
    }
    67% {
        transform: rotate3d(1,1,0,180deg);
    }
    100%{
        transform: rotate3d(1,1,4,180deg);
    }
}

@keyframes slide {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0%);

    }
